.filter-container {
  
	margin-bottom: 50px;
}
.flex-row
{
display: -ms-flexbox;
  display: flex;
 -ms-flex-direction: row;
     flex-direction: row;
 -ms-flex-align: center;
     align-items: center;
 -ms-flex-pack: center;
     justify-content: center;
-ms-flex-wrap: wrap;
    flex-wrap: wrap;
}

.filter
{
	font-family: Nunito,sans-serif;
	cursor: default;
	-webkit-transition: all .3s ease-in-out;
	-o-transition: all .3s ease-in-out;
	transition: all .3s ease-in-out; 
	display: inline-block;
	margin: 0 20px 0;
	color: black;
	min-width: 50px;
	text-align: center;
	font-size: 25px;
}
.filter:hover { 
	-webkit-transform: scale(1.1); 
	    -ms-transform: scale(1.1); 
	        transform: scale(1.1); 
}

.filter-enabled
{
	font-family: Nunito,sans-serif;
	cursor: default;
	-webkit-transition: all .3s ease-in-out;
	-o-transition: all .3s ease-in-out;
	transition: all .3s ease-in-out; 
	display: inline-block;
	margin: 0 20px 0;
	color: black;
	min-width: 50px;
	text-align: center;
	font-size: 25px;
	 background-color: rgb(200, 200, 200);
}

.cross
{
	width: 50px;
	height: 50px;
}

.closebtn {
	text-align: center;
    color: red;
    font-weight: bold;
    float: right;
    font-size: 22px;
    cursor: pointer;
}




